import React from 'react';
import { ViewAccount } from './ViewAccount';
import { EditAccount } from './EditAccount';

interface IProps {
    mode: string
    userData: any
    setMode: (e) => void
    userIdDb: string
    setUserData: (value: any) => void
    updateStates: any
}

export const AccountTab: React.FC<IProps> = (props: IProps):JSX.Element => {
    const {
        userData,
        mode,
        setMode,
        userIdDb,
        setUserData,
        updateStates
    } = props;

    return (
        <>
            {
                mode === 'view' ?
                    <ViewAccount
                        setUserData={setUserData}
                        userData = {userData}
                        userIdDb = {userIdDb} /> 
                : mode === 'edit' ?
                    <EditAccount
                        updateStates={updateStates}
                        setUserData={setUserData}
                        setMode={setMode}
                        userData = {userData}/> 
                : ''
            }
        </>
    )
}