
import * as React from 'react';
import {
    Grid,
    Typography,
    Link,
    Button
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
// import { ChangePassword } from './ChangePassword';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';
import { resetPassword, unlockUser } from 'Pages/API/Calls/User';
import moment from 'moment'
import { LoadingIndicator } from 'Components/UI';
import LockIcon from '@material-ui/icons/Lock';


const useStyles = makeStyles(() => ({
    label: {
        color: '#3C394A',
        fontSize: 12,
        opacity: .6
    },
    value: {
        color: '#3C394A',
        fontSize: 16,
        fontWeight: 800
    },
    linkAction: {
        color: '#3AB77D',
        textDecoration: 'underline',
        fontSize: 12,
        cursor: 'pointer'
    },
    button: {
        margin: '0 10px',
        minHeight: '42px',
        minWidth: '150px',
        fontSize: 14
    },
    circle: {
        width: 8,
        height: 8,
        background: '#3C394A',
        borderRadius: '100%',
        display: 'inline-block',
        marginRight: 3,
        marginTop: 5
    },
    padlock: {
        color: '#3AB77D',
        fontSize: 18,
        position: 'relative',
        top: -3,
        marginRight: 5
    }
}));

interface IViewAccountProps {
    userData: any
    userIdDb: string
    setUserData: (value: any) => void
}

export const ViewAccount: React.FC<IViewAccountProps> = (props: IViewAccountProps): JSX.Element => {
    const classes = useStyles();
    // const [ChangePasswordModalVisibility, setChangePasswordModalVisibility] = React.useState(false)
    const [resetModalOpen, setResetModalOpen] = React.useState(false);
    const [resetSuccessModalOpen, setResetSuccessModalOpen] = React.useState(false);
    const [loading, setLoading] = React.useState(false)
    const [unlockModal, setUnlockModal] = React.useState(false)
    const [unlockModalProps, setUnlockModalProps] = React.useState<any>({
        open: false,
        title: '',
        message: ''
    })
    // const [currentDate, setCurrentDate] = React.useState()
    const openResetModal = () => {
        setResetModalOpen(true)
    }

    const closeUnlockModalProps = () => {
        setUnlockModalProps({...unlockModalProps, open: false})
    }

    const handleOnResetPassword = () => {
        setLoading(true)
        resetPassword(props.userData.username).then(response => {
            console.log(response);
            setResetModalOpen(false);
            setResetSuccessModalOpen(true);
            setLoading(false);
        })
    }

    const openUnlockModal = () => {
        setUnlockModal(true);
    }

    const closeUnlockModal = () => {
        setUnlockModal(false);
    }

    const unlockOnSubmit = () => {
        setLoading(true);
        closeUnlockModal();
        unlockUser(props.userData._id).then((res) => {
            if (!res.error) {
                setUnlockModalProps({
                    ...unlockModalProps,
                    open: true,
                    title: 'Unlock User Success',
                    message: 'This user has been successfully unlocked.'
                })
                props.setUserData(res);
            } else {
                setUnlockModalProps({
                    ...unlockModalProps,
                    open: true,
                    title: 'Error',
                    message: res.error.message
                })
            }

            setLoading(false);
        }).catch((err) => {
            setLoading(false);
            setUnlockModalProps({
                ...unlockModalProps,
                open: true,
                title: 'Error',
                message: err.message
            })
        })
    }

    return (
        <>
            {loading ?
                (<LoadingIndicator />) : null
            }
            <Grid container spacing={5}>
                <Grid item xs={12}>
                    <Typography><b>ACCOUNT</b></Typography>
                </Grid>
                <Grid item xs={6}>
                    <Typography className={classes.label}>USERNAME</Typography>
                    <Typography className={classes.value}>{props.userData.username}</Typography>
                </Grid>
                <Grid item xs={6}>
                    <Typography className={classes.label}>PRIMARY ROLE AND MODULE</Typography>
                    <Typography className={classes.value}>{props.userData.main_role && props.userData.main_role.name + " - " + props.userData.main_module}</Typography>
                </Grid>
            </Grid>
            <Grid container spacing={5}>
                <Grid item xs={6}>
                    <Typography className={classes.label}>EMAIL</Typography>
                    <Typography className={classes.value}>{props.userData.email}</Typography>
                </Grid>
                <Grid item xs={6}>
                    <Typography className={classes.label}>USER GROUP</Typography>
                    <Typography className={classes.value}>{props.userData.group == null || undefined ? "N/A" : props.userData.group.name}</Typography>
                </Grid>
            </Grid>
            <Grid container spacing={5}>
                <Grid item xs={6}>
                    <Typography className={classes.label}>PASSWORD</Typography>
                    <Typography className={classes.value}>
                        <span className={classes.circle}></span><span className={classes.circle}></span><span className={classes.circle}></span>
                        <span className={classes.circle}></span><span className={classes.circle}></span><span className={classes.circle}></span>
                    </Typography>
                    <Link onClick={() => {
                        openResetModal()
                    }}>Reset Password</Link>

                </Grid>
                <Grid item xs={6}>
                    <Typography className={classes.label}>ROLES FROM USER GROUPS</Typography>
                    <Typography className={classes.value}><u>
                        {
                            props.userData.hasOwnProperty('group') && props.userData.group.hasOwnProperty('roles') && props.userData.group.roles.length > 0 ?
                                props.userData.group.roles.map((d, i) =>
                                    Object.keys(d).length > 0 ?
                                        `${d.name}${i !== (props.userData.group.roles.length - 1) ? ', ' : ''}`
                                        : 'N/A'
                                ) : 'N/A'
                        }
                    </u></Typography>
                </Grid>
            </Grid>
            <Grid container spacing={5} >
                <Grid item xs={6}>
                    <Typography className={classes.label}>STATUS</Typography>
                    <Typography className={classes.value} style={{ textTransform: 'capitalize' }}>
                        <b>{props.userData.status}</b>
                    </Typography>
                    {props.userData.status === 'locked' ? <Link onClick={openUnlockModal}><LockIcon className={classes.padlock} />Unlock User</Link> : ''}
                </Grid>
                <Grid item xs={6}>
                    <Typography className={classes.label}>OTHER ROLES</Typography>
                    <Typography className={classes.value}>
                        <u>
                            {
                                props.userData.hasOwnProperty('roles') ? props.userData.roles.length > 0 ?
                                    props.userData.roles.map((d, i) =>
                                        `${d.name}${i !== (props.userData.roles.length - 1) ? ', ' : ''}`
                                    ) : 'N/A' : 'N/A'
                            }
                        </u>
                    </Typography>
                </Grid>
            </Grid>

            <Grid container spacing={5}>
                <Grid item xs={3}>
                    <Typography className={classes.label}>SUPERVISOR</Typography>
                    {
                        props.userData.reports_to ?
                            <>
                                <Typography className={classes.value}>{props.userData.reports_to.first_name} {props.userData.reports_to.last_name}</Typography>
                                <Typography style={{ color: 'rgba(60, 57, 74, 0.7)' }}><small><b>{props.userData.reports_to.job_title ? props.userData.reports_to.job_title : 'N/A'}</b></small></Typography>
                                <Typography style={{ color: 'rgba(60, 57, 74, 0.7)' }}><small><i>{props.userData.reports_to.employee_id ? props.userData.reports_to.employee_id : 'N/A'}</i></small></Typography>
                            </>
                            : <Typography className={classes.value}>N/A</Typography>
                    }
                </Grid>
            </Grid>
            <Grid container spacing={5} style={{ paddingBottom: 30 }}>
                <Grid item xs={3}>
                    <Typography className={classes.label}>EFFECTIVITY DATE</Typography>
                    <Typography className={classes.value}>{props.userData.effectivity_date ? moment(props.userData.effectivity_date).format('MMM DD, YYYY') : 'N/A'}</Typography>
                </Grid>
                <Grid item xs={3}>
                    <Typography className={classes.label}>EXPIRY DATE (If applicable)</Typography>
                    <Typography className={classes.value}>{props.userData.expiry_date ? moment(props.userData.expiry_date).format('MMM DD, YYYY') : 'N/A'}</Typography>
                </Grid>
            </Grid>

            <Grid container spacing={5}>
                <Grid item xs={12}>
                    <Typography><b>PROFILE</b></Typography>
                </Grid>
                <Grid item xs={3}>
                    <Typography className={classes.label}>FIRST NAME</Typography>
                    <Typography className={classes.value}>{props.userData.first_name ? props.userData.first_name : "N/A"}</Typography>
                </Grid>
                <Grid item xs={3}>
                    <Typography className={classes.label}>MIDDLE NAME</Typography>
                    <Typography className={classes.value}>{props.userData.middle_name ? props.userData.middle_name : "N/A"}</Typography>
                </Grid>
                <Grid item xs={3}>
                    <Typography className={classes.label}>LAST NAME</Typography>
                    <Typography className={classes.value}>{props.userData.last_name ? props.userData.last_name : "N/A"}</Typography>
                </Grid>
            </Grid>
            <Grid container spacing={5}>
                <Grid item xs={3}>
                    <Typography className={classes.label}>EMPLOYEE ID NUMBER</Typography>
                    <Typography className={classes.value}>{props.userData.employee_id ? props.userData.employee_id : "N/A"}</Typography>
                </Grid>
                <Grid item xs={3}>
                    <Typography className={classes.label}>DEPARTMENT</Typography>
                    <Typography className={classes.value}>{props.userData.department ? props.userData.department.name : "N/A"}</Typography>
                </Grid>
                <Grid item xs={3}>
                    <Typography className={classes.label}>JOB POSITION / RANK</Typography>
                    <Typography className={classes.value}>{props.userData.job_title ? props.userData.job_title : "N/A"}</Typography>
                </Grid>
            </Grid>
            <Grid container spacing={5}>
                <Grid item xs={3}>
                    <Typography className={classes.label}>SECTION</Typography>
                    <Typography className={classes.value}>{props.userData.section ? props.userData.section : "N/A"}</Typography>
                </Grid>
                <Grid item xs={3}>
                    <Typography className={classes.label}>LOCATION</Typography>
                    <Typography className={classes.value}>{props.userData.location ? props.userData.location : "N/A"}</Typography>
                </Grid>
            </Grid>
            {/* BEGIN MODALS */}


            <ModalComponent
                isModalOpen={resetModalOpen}
                onClose={() => setResetModalOpen(false)}
                message={'Do you really want to reset the password of this user?'}
                title={'Reset Password'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button onClick={() => { setResetModalOpen(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}
                                onClick={() => {
                                    handleOnResetPassword()
                                }}
                            >Reset</Button>
                        </Grid>
                    </Grid>
                }
            />
            {/* Reset modal */}
            <ModalComponent
                isModalOpen={resetSuccessModalOpen}
                onClose={() => setResetSuccessModalOpen(false)}
                message={"New password has been sent to the user's registered email account."}
                title={'Reset Password Success'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}
                                onClick={() => {
                                    setResetSuccessModalOpen(false)
                                }}
                            >Okay</Button>
                        </Grid>
                    </Grid>
                }
            />
            
            {/* Unlock modal */}
            <ModalComponent
                isModalOpen={unlockModal}
                onClose={closeUnlockModal}
                message={`Are you sure you want to grant ${props.userData.first_name} access to the system again?`}
                title={'Unlock User'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button onClick={closeUnlockModal} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}
                                onClick={() => {
                                    unlockOnSubmit()
                                }}
                            >Unlock</Button>
                        </Grid>
                    </Grid>
                }
            />

            <ModalComponent
                isModalOpen={unlockModalProps.open}
                onClose={closeUnlockModalProps}
                message={unlockModalProps.message}
                title={unlockModalProps.title}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}
                                onClick={closeUnlockModalProps}
                            >Okay</Button>
                        </Grid>
                    </Grid>
                }
            />
        </>
    )
}