export const Account: React.FC = (props: any): JSX.Element => {
    const classes = accountsPageStyles();
    const classes2 = useStyles();
    React.useEffect(() => {
        fetchUser();
        getUserIdDb();
    }, [])

    const fetchUser = async () => {
        setIsFetching(true)
        let user = await getUserDetails(getLastSegment())
            .catch((err) => {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Error',
                    message: err.message
                })
            })

        if (user) {
            setPhoto(user.body.profile_pic)
            setUserDetails(user.body);
        }
        setIsFetching(false)
    }

    const handleClose2 = () => {
        setModalProps({
            ...modalProps,
            open: false
        })
    };

    const getUserIdDb = () => {
        Utils.StorageService('user_data', 'user_id').then(response => {
            setUserIdDb(response.result);
            console.log('reponse', response, 'wew')
        })
    }

    const editRole = () => {
        setAccountMode('edit');
    }

    const activeSideBarItem = (item) => {
        setActiveSidebar(item);
    }

    const handleAccountPhoto = (event: any) => {
        const validFiles = ['image/jpeg', 'image/jpg,', 'image/png'];

        if (validFiles.includes(event[0].type)) {
            setPhoto(event[0]);
        } else {
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Error selecting image',
                message: 'Invalid file extension.',
                method: handleClose2
            })
        }
    };

    const closeUploadModal = () => {
        setUploadModal(false)
        setPhoto(null)
    }

    const handleUpload = () => {
        let reader = new FileReader();
        reader.readAsDataURL(photo);
        reader.onload = () => {
            setUserDetails({
                ...userDetails,
                profile_pic: (reader.result as string).split(',')[1]
            })
            closeUploadModal();
        };
    }


    // DECLARE STATES
    const [photo, setPhoto] = React.useState<any>();
    const [uploadModal, setUploadModal] = React.useState(false);
    const [userIdDb, setUserIdDb] = React.useState('');
    const [isFetching, setIsFetching] = React.useState(false);
    const [userDetails, setUserDetails] = React.useState<IAccount.userDetails>({
        department: '',
        email: '',
        employee_id: '',
        first_name: '',
        group: {},
        is_locked: false,
        job_title: '',
        last_name: '',
        location: '',
        main_module: '',
        main_role: '',
        middle_name: '',
        position: '',
        profile_pic: '',
        role: [],
        section: '',
        status: '',
        user_id: '',
        username: '',
        _id: '',
    });

    const [activeSidebar, setActiveSidebar] = React.useState<any>();
    const [accountMode, setAccountMode] = React.useState<string>('view');
    const [modalProps, setModalProps] = React.useState<any>({
        open: false,
        title: '',
        message: '',
        onOkay: () => { setModalProps({ ...modalProps, open: false }) }
    })

    const editIconContainer = {
        position: 'absolute',
        right: '15px',
        bottom: '15px',
        background: '#3AB77D',
        borderRadius: '100%',
        width: 24,
        height: 24,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        cursor: 'pointer'
    }

    return (
        <>
            {isFetching && <UI.LoadingIndicator />}
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                open={uploadModal}
                className={classes2.modal}
                onClose={closeUploadModal}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}>
                <Fade in={uploadModal}>
                    <div className={classes2.paper}>
                        <List>
                            <ListItemText>
                                <Link
                                    className={classes2.closeButton}
                                    onClick={closeUploadModal}
                                >
                                    <FontAwesomeIcon icon={faTimes} />
                                </Link>
                            </ListItemText>
                        </List>
                        <p id="transition-modal-title"
                            style={{ fontSize: "20px" }}>
                            <b>Upload Picture</b>
                        </p>
                        <Dropzone
                            multiple={false}
                            onDrop={(event) => {
                                // const base64 = getBase64(event)
                                handleAccountPhoto(event)
                            }}>
                            {({ getRootProps, getInputProps }) => (
                                <section {...getRootProps({ className: `dropzone ${classes.dropzone}` })}>
                                    <div >
                                        <input {...getInputProps()}
                                        />
                                        <div className={classes2.centerDrop}>
                                            <FontAwesomeIcon
                                                style={{ opacity: 0.56 }}
                                                icon={faFileUpload}
                                                color='#272E4C'
                                                size='2x' />
                                            <div className={classes2.centerDropLabels}>
                                                {
                                                    photo && photo instanceof File ? photo.name :
                                                        <>
                                                            <span className={classes2.centerDropLabel}>Drag or drop file.</span>
                                                            <span className={classes2.centerDropLabel}>.jpg or .png</span>
                                                        </>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </section>
                            )}
                        </Dropzone>
                        <Grid container spacing={1}>
                            <Grid item xs={6}>
                                <Button
                                    className={classes2.cancelButton}
                                    variant="outlined"
                                    color="secondary"
                                    onClick={closeUploadModal}>
                                    Cancel
                        </Button>
                            </Grid>
                            <Grid item xs={6}>
                                <Button
                                    // onClick={handleOpen2}
                                    onClick={handleUpload}
                                    className={classes2.saveButton}
                                    variant="contained"
                                    color="primary">
                                    Upload
                      </Button>
                            </Grid>
                        </Grid>
                    </div>
                </Fade>
            </Modal>

            <SubHeader
                headingTitle={userDetails.username}
                breadCrumbsArray={pageConst.breadCrumbs}
                buttonFunc={editRole}
                buttonName={'Edit Account'}
                layoutType={'layout_1'}
                enableButton={accountMode === 'edit' ? false : true} />

            <Grid
                className={classes.bodyContainer}
                container
                xs={12}>
                <Grid
                    item
                    xs={3}
                    lg={2}
                    className={classes.sideBarContainer}>
                    <IconButton edge="end" onClick={() => { }}>
                        <Avatar
                            className={classes.userImage}
                            src={userDetails.profile_pic !== 'DEFAULT_PIC' ? `data:image/jpeg;base64,${userDetails.profile_pic}` : 'user.jpg'}
                            alt="avatar" />
                        {
                            accountMode === 'edit' &&
                            <span
                                onClick={() => setUploadModal(true)}
                                //@ts-ignore
                                style={editIconContainer}>
                                <CreateIcon style={{
                                    color: '#ffffff',
                                    width: 13,
                                    height: 13
                                }} />
                            </span>
                        }
                    </IconButton>
                    <SideMenu
                        items={pageConst.sideBarItems}
                        activeItems={activeSideBarItem}
                    ></SideMenu>
                </Grid>
                <Grid item xs={9} lg={10} className={classes.contentContainer}>
                    <AccountTab
                        updateStates={props.updateStates}
                        setMode={setAccountMode}
                        userIdDb={userIdDb}
                        userData={userDetails}
                        setUserData={setUserDetails}
                        mode={accountMode} />
                </Grid>
                {activeSidebar}
            </Grid>

            <UI.MsgModal
                isModalOpen={modalProps.open}
                title={modalProps.title}
                message={modalProps.message}
                onClose={modalProps.onOkay}
            />
        </>
    )
}

const mapStateToProps = (state: Store) => state.users;

const mapDispatchToProps = (dispatch: Dispatch) => ({
    Map: bindActionCreators(AccountActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };