    const validationAlgorithm = (bodyRequest) => { 
        // let preValidObject = {}
        let optionalFields = ['section', 'group', 'profile_pic', 'reports_to', 'expiry_date', 'is_active', 'is_locked', 'retry_count', 'roles'];
        let valid: any = [];
        let invalid: any = [];
        
        let payloadArray = Object.entries(bodyRequest);
    
        payloadArray.forEach((payloadObject: any) => {
          if (!optionalFields.includes(payloadObject[0])) {
            if (payloadObject[1]) {
                if (payloadObject[0] === 'email') {
                    if (validEmail) {
                        payloadObject[1] = true
                        valid.push(payloadObject);
                    } else {
                        payloadObject[1] = false
                        invalid.push(payloadObject);
                    }
                } else {
                    payloadObject[1] = true
                    valid.push(payloadObject);
                }
            } else {
                payloadObject[1] = false
                invalid.push(payloadObject);
            }
          }
        })
    
        let preValid: any = Object.assign(Object.fromEntries(valid), Object.fromEntries(invalid));
    
        setFieldValidation(preValid);

        console.log(preValid);
        setErrorMessages({
          first_name: !preValid.first_name ? 'Invalid Input' : '',
          middle_name: !preValid.middle_name ? 'Invalid Input' : '',
          last_name: !preValid.last_name ? 'Invalid Input' : '',
          employee_id: !preValid.employee_id ? errorMessages.employee_id === '' ? 'Invalid Input' : errorMessages.employee_id : '',
          department: !preValid.department ? 'Invalid Input' : '',
          job_title: !preValid.job_title ? 'Invalid Input' : '',
          email: !preValid.email ? errorMessages.email  === '' ? 'Invalid Input' : errorMessages.email : '',
          location: !preValid.location ? 'Invalid Input' : '',
          effectivity_date: !preValid.effectivity_date ? 'Invalid Input' : '',
          main_module: !preValid.main_module ? 'Select a Primary Role and Module' : '',
          main_role: !preValid.main_role ? 'Select a Primary Role and Module' : '',
        })

        

        for(var f in preValid)
        if(!preValid[f]) return false;
            
        return true;
    }

